body {
  letter-spacing: -.04em;
  color: #111;
  font-family: Open Sans, sans-serif;
  font-weight: 400;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

button {
  cursor: pointer;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: none;
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 24px;
  }
}

.header {
  background-color: #bb0a1e;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__menu {
  color: #fff;
  background-color: inherit;
  border: none;
  margin: 0;
  padding: 2px 0 0;
}

.header__logo {
  color: #fff;
  padding: 15px 0;
  font-family: Dancing Script, cursive;
  font-size: 29px;
  font-weight: 700;
}

.networks {
  align-items: center;
  gap: 10px;
  display: flex;
}

.networks__link {
  color: #fff;
}

.networks__icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
  background-color: inherit;
}

.mobile-menu {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.mobile-menu__btn {
  color: #696666;
  background-color: inherit;
  border: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 10px;
  right: 10px;
}

.mobile-menu__btn:hover, .mobile-menu__btn:focus {
  color: #bb0a1e;
}

.mobile-menu__btn--icon {
  fill: currentColor;
  width: 20px;
  height: 20px;
  padding: 0;
}

.mobile-menu__wrapper {
  width: 250px;
  height: 100vh;
  z-index: 2;
  background-color: #f4f5f5;
  padding: 20px 0;
  position: absolute;
  top: 0;
  left: 0;
}

.is-open {
  transform: translateX(0);
}

.nav__list {
  margin-bottom: 10px;
}

.nav__item {
  color: #696666;
  text-transform: uppercase;
  border-bottom: 1px solid #ebe5e5;
  padding: 15px 10px;
  font-size: 18px;
  font-weight: 700;
}

.phone, .location {
  border-bottom: 1px solid #ebe5e5;
  align-items: center;
  gap: 10px;
  padding: 0 10px 10px;
  display: flex;
}

.phone__icon, .location__icon {
  width: 35px;
  height: 35px;
  fill: #bb0a1e;
}

.phone {
  margin-bottom: 10px;
}

